import VueSimpleSuggest from 'vue-simple-suggest'

export default {
  name: 'CategorySuggest',
  components: {
    VueSimpleSuggest
  },
  props: {
    minimal: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    }
  },
  computed: {
    categories() {
      return this.$store.getters.categories.map(el => el.name)
    }
  },
  methods: {
    onChange(e) {
      this.$emit('input', e)
    }
  }
}
